html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

.path-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
}

/* Style for individual card */
.path-card {
  width: calc(50% - 10px);
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

/* Style for card image */
.path-img {
  width: 100%;
  height: 3000px;
  background-size: cover;
  background-position: center;
  opacity: 0.8;
}

/* Style for card content */
.path-content {
  padding: 20px;
}
/* Style for card header */
.path-header {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

/* Style for card sub-header */
.path-subheader {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: #666;
}

/* Style for card paragraph */
.path-paragraph {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: #333;
  margin-top: 10px;
}
/* Style for calendar icon */
.calendar-icon {
  margin-right: 5px;
}

/* Media query for responsive layout */
@media screen and (max-width: 600px) {
  .path-card {
    width: 100%;
  }
}

